.App {
  min-height: 100vh;
  background: #00ff90b3;
}

.inputBox {
  position: relative;
}

.input {
  width: 100%;
  height: 65px;
  padding: 10px 70px 10px 10px;
  border-radius: 10px;
  font-size: 18px;
}

.submitBtn {
  position: absolute;
  width: 50px;
  height: 50px;
  margin: 7px 20px 7px 0px;
  border-radius: 10px;
  right: 0px;
  border: none;
  font-size: 15px;
  background-color: #2f74c0;
  color: aliceblue;
  transition: 0.2 all;
  box-shadow: 0 0 10px black;
}

.todoContainer {
  width: 90vw;
  margin: auto;
}

.todo {
  background-image: url("../public/todoBg.webp");
}

.todo textarea {
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #0101013d;
}

.actionButtons {
  height: 100%;
}

.actionButtons span {
  cursor: pointer;
}
